.contacts {
  padding-top: 114px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.contacts h2 {
  margin-bottom: 20px;
}
.contacts p {
  text-align: center;
  margin-bottom: 29px;
  font-size: 18px;
  line-height: 22px;
}
.contacts button {
  height: 42px;
  width: 180px;
  margin: auto;
  font-size: 18px;
  color: #fff;
  background-color: #000;
  border: none;
  border-radius: 30px;
  margin-bottom: 86px;
  transition: all 0.5s ease-out;
}
.contacts button:hover {
  color: #000;
  background-color: #fff;
  outline: solid 1px #000;
  cursor: pointer;
}
.contacts .social {
  max-width: 342px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 45px;
}
.contacts .social a {
  transition: all 0.5s ease-out;
}
.contacts .social a:hover {
  scale: 1.3;
}
